import { Component, OnInit, OnDestroy, Inject, HostListener } from '@angular/core';
import { trigger, style, animate, transition, query, stagger, keyframes, animateChild } from '@angular/animations';
import { DOCUMENT } from '@angular/common';

import { ExpenseService } from './../../core/services/expense.service';
import { LangService } from './../../core/services/references/lang.service';
import { WINDOW } from './../../core/services/window.service';

@Component({
  selector: 'itx-expense-list',
  templateUrl: './expense-list.component.html',
  styleUrls: ['./expense-list.component.scss'],
  animations: [
    trigger('slide', [
      transition(
        ':enter',
        [animate('250ms ease-in-out', keyframes([
          style({ transform: 'translateY(80%)', opacity: 0, offset: 0 }),
          style({ transform: 'translateY(-10%)', opacity: 0.8, offset: 0.7 }),
          style({ transform: 'translateY(5%)', opacity: 1, offset: 0.9 }),
          style({ transform: 'translateY(0)', opacity: 1, offset: 1 })
        ]))]
      ),
      transition(
        ':leave',
        [animate('250ms ease-in-out', keyframes([
          style({ transform: 'translateY(0)', opacity: 1, offset: 0 }),
          style({ transform: 'translateY(80%)', opacity: 0, offset: 1 })
        ]))]
      )
    ]),
    trigger('showCard', [
      transition(':enter', [ query(':enter', stagger(50, [animateChild()]), { optional: true })]),
      transition(':leave', [ query(':leave', stagger(50, [animateChild()]), { optional: true })])
    ]),
  ]
})
export class ExpenseListComponent implements OnInit, OnDestroy {

  public translation;

  public openRequests: Array<any> = [];
  public closedRequests: Array<any> = [];
  public requestStatusActiveTab: number;

  public requestOnLoad: boolean;

  public displayOpenRequests: boolean = true;
  public displayClosedRequests: boolean = true;

  private numberToLoad = 10;
  private count = 1;
  private loadAt = 300;

  private openRequestsObserver;
  private closedRequestsObserver;

  constructor(
    private expenseService: ExpenseService,
    private langService: LangService,
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window
  ) {
    this.translation = this.langService.lang;

    this.requestStatusActiveTab = 0;

    this.openRequestsObserver = this.expenseService.openRequests$.subscribe(requests => {
      this.openRequests.splice(this.openRequests.length - 10, 10);
      requests.forEach(request => {
        this.openRequests.push(request);
      });
      this.displayOpenRequests = this.openRequests.length > 0 ? true : false;
      this.requestOnLoad = false;
    });

    this.closedRequestsObserver = this.expenseService.closedRequests$.subscribe(requests => {
      this.closedRequests.splice(this.closedRequests.length - 10, 10);
      requests.forEach(request => {
        this.closedRequests.push(request);
      });
      this.displayClosedRequests = this.closedRequests.length ? true : false;
      this.requestOnLoad = false;
    });
  }

  ngOnInit(): void {
    this.getOpenHelpdeskRequests();
    this.getClosedHelpdeskRequests();
  }

  ngOnDestroy(): void {
    this.openRequestsObserver.unsubscribe();
    this.closedRequestsObserver.unsubscribe();
  }

  @HostListener('window:scroll', [])
  onWindowScroll(): void {
    const number: number = this.window.pageYOffset || this.document.documentElement.scrollTop || this.document.body.scrollTop || 0;
    if (number > this.count * this.loadAt && !this.requestOnLoad) {
      if (this.requestStatusActiveTab === 0) {
        this.getOpenHelpdeskRequests();
      } else {
        this.getClosedHelpdeskRequests();
      }
      this.count++;
    }
  }

  private getOpenHelpdeskRequests(): void {
    this.requestOnLoad = true;
    if (!this.openRequests[this.openRequests.length - 1]) {
      this.expenseService.getOpenExpenseRequests(0, this.numberToLoad);
    } else {
      this.expenseService.getOpenExpenseRequests(this.openRequests[this.openRequests.length - 1].ID, this.numberToLoad);
    }
    this.openRequests = this.openRequests.concat([{}, {}, {}, {}, {}, {}, {}, {}, {}, {}]);
  }

  private getClosedHelpdeskRequests(): void {
    this.requestOnLoad = true;
    if (!this.closedRequests[this.closedRequests.length - 1]) {
      this.expenseService.getClosedExpenseRequests(0, this.numberToLoad);
    } else {
      this.expenseService.getClosedExpenseRequests(this.closedRequests[this.closedRequests.length - 1].ID, this.numberToLoad);
    }
    this.closedRequests = this.closedRequests.concat([{}, {}, {}, {}, {}, {}, {}, {}, {}, {}]);
  }

  public activateTab(tab: number): void {
    this.requestStatusActiveTab = tab;
    this.count = 1;
  }

  public trackByRequest(index: number, request: any): number {
    return index;
  }

}
